import React, { useRef, useLayoutEffect, useEffect } from "react";
import "./userProfileSidebar.styles.css";
import ProfileImage from "../../assets/Profile.png";
import SideBar from "../sideBarComponent/sideBarComponent";

import { useSelector } from "react-redux";
const UserProfileSidebar = () => {
  let mobileNumber = useSelector((state) => state.User.currentUser.phoneNumber);
  return (
    <div className="HomeGrid">
      <p className="ppulse">Priority Pulse</p>
      <div className="UserProfileSidebar">
        <p className="priorityPulse">Priority Pulse</p>
        <div className="profile">
          <img src={ProfileImage} alt="ProfileImage" className="profileImage" />
          <div className="profileDetails">
            <div className="profileDetail">
              <p>Contact</p>
            </div>
            <div className="profileDetail">
              <p>:</p>
            </div>
            <div className="profileDetail">
              <p>{mobileNumber}</p>
            </div>
          </div>
        </div>
      </div>
      <div className="mapGrid">
        <SideBar />
      </div>
    </div>
  );
};

export default UserProfileSidebar;
