import React from "react";
import "./TotalDetail.styles.css";

const TotalDetail = ({
  detailHeading,
  detailName,
  contactNo,
  address,
  caseName,
  age,
  id
}) => {
  return (
    <div className="TotalDetail">
      <p className="totalDetailTitle">{detailHeading}</p>
      <p className="totalDetailName">{detailName}</p>
      <div className="totalDetailContactWrapper">
        <p className="totalDetailContact">Contact</p>
        <p>:</p>
        <p className="totalDetailContactNo">{contactNo}</p>
      </div>
      <div className="totalDetailCaseOrAddressWrapper">
        <p className="totalDetailAddress">{address ? "Address" : "Case"}</p>
        <p>:</p>
        <p className="totalDetailAddressOrCase">
          {address ? address : caseName}
        </p>
      </div>
    </div>
  );
};

export default TotalDetail;
