import React from "react";
import "./footer.styles.css";
import fbLogo from "../../assets/facebook-brands.svg";
import lnkdLogo from "../../assets/linkedin-brands.svg";
import instLogo from "../../assets/instagram.svg";
import twitLogo from "../../assets/twitter-square-brands.svg";

const Footer = () => {
  return (
    <div className="footer">
      <div className="footer-left-wrapper">
        <p className="prioritypulseFooter">Priority pulse</p>
        <p className="copyRights">&#169; 2020 All rights reserved</p>
      </div>
      <div className="footer-right-wrapper">
        <p className="ppgmail">prioritypulse@gmail.com</p>
        <div className="socialmedialinks-wrapper">
          <ul className="socialMediaList">
            <li>
              <img
                src={fbLogo}
                className="socialmediaicon"
                alt="prioritypulse facebook"
              />
            </li>
            <li>
              <img
                src={lnkdLogo}
                className="socialmediaicon"
                alt="prioritypulse linkedin"
              />
            </li>
            <li>
              <img
                src={instLogo}
                className="socialmediaicon"
                alt="prioritypulse instagram"
              />
            </li>
            <li>
              <img
                src={twitLogo}
                className="socialmediaicon"
                alt="prioritypulse twitter"
              />
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Footer;
