import React from "react";
import "./aboutUs.styles.css";
import { ReactComponent as TeamImg } from "../../assets/Teampic.svg";

const AboutUs = () => {
  return (
    <div className="aboutus-wrapper">
      <p className="aboutpriorityPulse">Priority Pulse</p>
      <div className="AboutUs">
        <div className="teamPicWrapper">
          <TeamImg className="teamImg" />
        </div>
        <div className="aboutUsWrapper">
          <p className="teamTitle">The Team</p>

          <p className="theTeamDes">
            An issue that is never ending in our country is the traffic. Our
            team Priority plus is reaching out to this issue with a priority
            based solution. The core problem with traffic arises when an
            ambulance tries to cross over. That is when the real struggle delay
            and the arrival of the ambulance. This helps in an automatic control
            of the signals. When the ambulance reaches a certain range, the
            signal turns green and holds other signals in red, which will let
            the traffic stop and ambulance to pass through. Just like Mother
            Teresa said, Helping Hands are better than praying lips, Priority
            plus is in service with a priority provision.
          </p>
        </div>
      </div>
    </div>
  );
};

export default AboutUs;
