import React, { useEffect } from "react";
import UserProfileSidebar from "../userProfileSidebar/userProfileSidebar";

import { useSelector, useDispatch } from "react-redux";
import { fetchPastRideDetails } from "../../redux/Rides/rides.actions";
import { fetchPresentRide } from "../../redux/PresentRide/presentRide.actions";
const Homepage = () => {
  let dispatch = useDispatch();
  let mobileNumber = useSelector((state) => state.User.currentUser.phoneNumber);
  useEffect(() => {
    dispatch(fetchPastRideDetails(mobileNumber.slice(3, 13)));
    dispatch(fetchPresentRide(mobileNumber.slice(3, 13)));
  }, []);
  return <UserProfileSidebar />;
};

export default Homepage;
