import React, { useEffect } from "react";
import "./hospitalNames.styles.css";
import { useSelector, useDispatch } from "react-redux";
import {
  fetchHospitalDetails,
  setHospitalCoordinates,
} from "../../redux/Hospital/hospitalActions";
import { getHospitalDetails } from "../../redux/Hospital/hospitalActions";

const HospitalNames = () => {
  const hospitalDetails = useSelector(
    (state) => state.Hospital.hospitalDetails
  );
  const hospitalCoordinates = useSelector((state) =>
    state.Hospital.aboutHospital.length > 0
      ? state.Hospital.aboutHospital[0].hospitalLocation.coordinates
      : []
  );
   console.log("hospitalCoordinates", hospitalCoordinates);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchHospitalDetails());
  }, []);

  return (
    <div className="HospitalNames">
      <p className="hospitalNamesTitle">Hospital names</p>
     
      <div className="hospitalnames">
        {hospitalDetails.length > 0 ? (
          <ul className="hospitalNamesList">
            {hospitalDetails.map((hospitalDetail) => (
              <li
                className="hospitalName"
                key={hospitalDetail._id}
                onClick={() => {
                  dispatch(getHospitalDetails(hospitalDetail.code));
                  // dispatch(setHospitalCoordinates(hospitalCoordinates));
                }}
              >
                {hospitalDetail.name}
              </li>
            ))}
          </ul>
        ) : (
          "There are no nearby hospitals"
        )}
      </div>
    </div>
  );
};

export default HospitalNames;
