import React, { useEffect } from "react";
import "./presentRide.styles.css";
import { useSelector, useDispatch } from "react-redux";
// import { rides } from "../../redux/Rides/rides.selector";
import { getRideDetails,getCurrentRideDetails } from "../../redux/Rides/rides.actions";
import {getPresentRideDetails} from '../../redux/PresentRide/presentRide.actions'
// import { fetchPresentRide } from "../../redux/PresentRide/presentRide.actions";

const PresentRide = () => {
  const presentRideDetails = useSelector(
    (state) => state.PresentRide.presentRide
  );


  // const currentRideDetails = useSelector((state)=> state.Rides.currentRideDetails)
  console.log(presentRideDetails)
   const number = useSelector((state) => state.User.currentUser.phoneNumber);
  // console.log(`${number.splice(0, 2)}`);
  //console.log(number.slice(3,13))
  const dispatch = useDispatch();
  useEffect(() => {
   //dispatch(getPresentRideDetails(presentRideDetails));
  }, []);
  return (
    <div className="presentRideDetails">
      {presentRideDetails.length > 0 ? (
        <table className="presentrideTable">
          <tbody>
            {presentRideDetails.map((presentRide, index) => (
              <tr
                className="presentrideDetails"
                key={index}
                onClick={() => {dispatch(getCurrentRideDetails([presentRide]))
                  var somepolyline = JSON.stringify(presentRide.polyline)
                 // alert(presentRide.activeStatus )
                 console.log(PresentRide)
                }}
              >
                <td>07:00</td>
              <td>{presentRide.name}</td>
                <td>{presentRide.pcase}</td>
                <td>{presentRide.pickedBy.name}</td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        <div className="noRides">Currently there are no rides</div>
      )}
    </div>
  );
};

export default PresentRide;
