import React, { useState, useEffect } from "react";
import "./login.styles.css";
import Advertisement from "../advertisement/advertisementComponent";
import OtpInput from "react-otp-input";
import { useDispatch, useSelector } from "react-redux";
import {
  setCurrentUser,
  setUserLatitudeLocation,
  setUserLongitudeLocation,
  postUserDetails,
} from "../../redux/User/user.actions";
import config from "../../firebase/firebaseconfig";
import firebase from "firebase";
import * as firebaseui from "firebaseui";
// import * as Location from "expo-location";

const Login = () => {
  const dispatch = useDispatch();
  const latitude = useSelector((state) => state.User.latitude);
  const longitude = useSelector((state) => state.User.longitude);
  useEffect(() => {
    if (!firebase.apps.length) {
      firebase.initializeApp(config);
    }
    const uiconfig = {
      signInSuccessUrl: "http://localhost:3000/home",
      signInOptions: [
        {
          provider: firebase.auth.PhoneAuthProvider.PROVIDER_ID,
          defaultCountry: "IN",
        },
      ],
      callbacks: {
        signInSuccess: (user) => {
          const userDetails = {
            number: user.phoneNumber.slice(3, 13),
          };
          fetch("https://serverprioritypulse.herokuapp.com/user/add", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(userDetails),
          })
            .then((res) => res.json())
            .then(dispatch(setCurrentUser(user)))
            .catch((err) => alert(err));
          dispatch(setCurrentUser(user));
        },
      },
      // tosUrl: "http://localhost:3000/home",
    };
    var ui = new firebaseui.auth.AuthUI(firebase.auth());
    ui.start("#loginFormWrapper", uiconfig);
    return () => {
      ui.delete();
    };
  }, []);
  return (
    <div className="loginGrid">
      <div className="LoginSidebar">
        <p className="priorityPulse">Priority Pulse</p>
        <div className="loginFormWrapper" id="loginFormWrapper"></div>
      </div>

      <div className="adGrid">
        <Advertisement />{" "}
      </div>
    </div>
  );
};
export default Login;
