import React from "react";
import "./TotalDetails.styles.css";
import TotalDetail from "../TotalDetailComponent/TotalDetailComponent";
import { useSelector } from "react-redux";

const TotalDetails = () => {
  const pastrideDetails = useSelector((state) => state.Rides.currentRideDetails);

  console.log(pastrideDetails)
  //const currentRideDetails = useSelector((state)=> state.Rides.currentRideDetails)
  return (
   <div>
    { pastrideDetails.length >0  ? (

<div className="TotalDetails">
 
  <TotalDetail 
    detailHeading="Patient Details"
    detailName={pastrideDetails[0].name}
    contactNo={pastrideDetails[0].patientNo}
    caseName={pastrideDetails[0].pcase}
    age={pastrideDetails[0].age}
  />
  <TotalDetail
    detailHeading="Driver Details"
    detailName={pastrideDetails[0].pickedBy.name}
    contactNo={pastrideDetails[0].pickedBy.mobileNo}
    id={pastrideDetails[0].pickedBy.hospital._id}
    address={pastrideDetails[0].pickedBy.hospital.address}
  />
  <TotalDetail
    detailHeading="Hospital Details"
    detailName={pastrideDetails[0].pickedBy.hospital.name}
    contactNo={pastrideDetails[0].pickedBy.hospital.hospitalNumbers[0]}
    address={pastrideDetails[0].pickedBy.hospital.address}
    />
</div>
) : (
<div className="TotalDetails">
  <TotalDetail
    detailHeading="Patient Details"
    detailName="Patient Name"
    contactNo="ContactNo"
    caseName="Case Name"
    age="age"
  />
  <TotalDetail
    detailHeading="Driver Details"
    detailName="Driver Name"
    contactNo="Contact No"
    address="Address"
  />
  <TotalDetail
    detailHeading="Hospital Details"
    detailName="Hospital Name"
    contactNo="Contact No"
    address="Address"
  />
</div>)
}
   </div>
  );
};

export default TotalDetails;

