import React, { useEffect, useState } from "react";
import "./hospitalDetail.styles.css";
import phoneicon from "../../assets/phone-solid.svg"; 
import { useSelector } from "react-redux";

const HospitalDetail = () => {
  // const dispatch = useDispatch();
  const hospitalDetails = useSelector((state) => state.Hospital.aboutHospital);
  const { name, address, hospitalNumbers } = { ...hospitalDetails[0] };
  let latitude = useSelector((state) => state.User.latitude);
  let longitude = useSelector((state) => state.User.longitude);
  let number = useSelector((state) =>
    state.User.currentUser.phoneNumber.slice(3, 13)
  );
  const [locationBtnName, setLocationBtnName] = useState(false);
  // console.log(user);
  // const sno = number.split("");
  // console.log({ ...hospitalDetails[0] });
  // const sno = number.map((snu) => snu);
  // console.log(`${number}`);
  console.log(hospitalNumbers);
   
  const handleSendLocation = () => {
    const user = {
      location: {
        coordinates: [latitude, longitude],
      },
      number: number,
    };

    fetch("https://serverprioritypulse.herokuapp.com/user/updatelocation", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(user),
    })
      .then(setLocationBtnName(true))
      .catch((err) => alert(err));
  };
  return (
    <div className="HospitalDetail">
      <div className="hdf">
        <p className="hospital">
          {hospitalDetails.length > 0 ? name : "Hospital Name"}
        </p>
        <button
          className="sendLocationBtn"
          onClick={handleSendLocation}
          style={{ cursor: "pointer" }}
        >
          {locationBtnName ? "Location sent" : "Send location"}
        </button>
      </div>
      <p className="hospitalAddress">
        {hospitalDetails.length > 0
          ? address
          : "Click on the hospital for more details"}
      </p>
      <div className="moreAboutHospitalDetails">
        <div className="moreAboutHospital">
          <p>Contact</p>
          <p>:</p>
          <p className="mahhc">
            {hospitalDetails.length > 0 ? hospitalNumbers.map((num)=>{
              return  <a className="for calling option" style={{color:'black',textDecoration:'none',alignItems:'center',margin:'8px'}} href={"tel:"+num} >{num}<img className="phoneicon" src={phoneicon} /><br /></a>
            }) : "Contact"}
          </p>
        </div>
        {/* <div className="moreAboutHospital">
        </div> */}
        <div className="moreAboutHospital">
          <p className="maha">Address</p>
                    
          <p className="madot">:</p>
          <p className="mahha">
            {hospitalDetails.length > 0 ? address : "Address"}
          </p>
          <br/>
   
        </div>
      </div>
    </div>
  );
};

export default HospitalDetail;
