import React, { useState } from "react";
// import logo from "./logo.svg";
import "./App.css";
// import LoginPage from "./pages/loginpage/loginPage";
import { Switch, Route, Redirect } from "react-router-dom";
import Navbar from "./components/navbarComponent/navbarComponent";
import Login from "./components/loginComponent/loginComponent";
// import Footer from "./components/footerComponent/footerComponent";
import UserProfileSidebar from "./components/userProfileSidebar/userProfileSidebar";
// import IndexMap from "./components/sideMapComponent/sideMapComponent";
import TrackAmbulancePage from "./components/trackAmbulanceComponent/trackAmbulanceComponent";
import AboutUs from "./components/aboutUsComponent/aboutUsComponent";
import Footer from "./components/footerComponent/footerComponent";
import { useSelector } from "react-redux";
import Homepage from "./components/Homepage/Homepage";

const App = () => {
  const user = useSelector((state) => state.User.currentUser);
  // console.log(user);

  // console.log(user);
  return (
    <div className="App">
      <div className="Grid">
        <div className="NavbarGrid">
          <Navbar currentUser={user} />
        </div>
        <div className="SidebarGrid">
          <Switch>
            <Route path="/" exact render={() => <Redirect to="/login" />} />
            <Route
              exact
              path="/login"
              render={() => (user ? <Redirect to="/home" /> : <Login />)}
            />
            <Route
              path="/home"
              render={() => (user ? <Homepage /> : <Redirect to="/login" />)}
            />

            <Route path="/aboutus" exact component={AboutUs} />
            <Route
              path="/trackambulance"
              render={() =>
                user ? <TrackAmbulancePage /> : <Redirect to="/login" />
              }
            />
            <Route path="/logout" render={() => <Redirect to="/login" />} />
          </Switch>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default App;
