import React, { useEffect } from "react";
import "./pastRide.styles.css";
import { useSelector, useDispatch } from "react-redux";
import { getRideDetails,getCurrentRideDetails } from "../../redux/Rides/rides.actions";
import { fetchPastRideDetails } from "../../redux/Rides/rides.actions";

const PastRide =  () => {

 // const currentRideDetails = useSelector((state)=> state.Rides.currentRideDetails)
  const pastRides = useSelector((state) => state.Rides.pastrides);
  let number = useSelector((state) => state.User.currentUser.phoneNumber);
  console.log(pastRides)
  // console.log(number.slice(3, 13));
  const dispatch = useDispatch();
  useEffect(() => {
    // dispatch(fetchPastRideDetails(number.slice(3, 13)));
  }, []);

  return (
    <div className="PastRide">
      {pastRides.length > 0 ? (
        <table className="pastrideTable">
          <tbody>
            {pastRides.map((pastRide, index) => (
              <tr
                className="pastrideDetails"
                onClick={() => {dispatch(getCurrentRideDetails([pastRide]))
                  var somepolyline = JSON.stringify(pastRide.polyline)
                 // alert(somepolyline)
                }}
                
                key={index}
              >
                <td>06:00</td>
                <td>{pastRide.name}</td>
                <td>{pastRide.pcase}</td>
                <td>{pastRide.pickedBy.name}</td>
               
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        <div className="noRides">
          There are no past rides available with the current number
        </div>
      )}
    </div>
  );
};

export default PastRide;
